import React from 'react'
import { Link, graphql } from 'gatsby'

import DefaultLayout from '../components/layout'
import Pagination from '../components/pagination'
import SEO from '../components/seo'
import { kebabCase } from 'lodash';

class BlogIndex extends React.Component {
  render() {
    const { data } = this.props
    const siteTitle = 'Home'
    const posts = data.allMarkdownRemark.edges
    const { currentPage, numPages } = this.props.pageContext
    const isFirst = currentPage === 1
    const isLast = currentPage === numPages
    const prevPage = currentPage - 1 === 1 ? '/' : (currentPage - 1).toString()
    const nextPage = (currentPage + 1).toString()

    return (
      <DefaultLayout>
        <SEO
          title={siteTitle}
          keywords={[`blog`, `azure`, `develop`, `cloud`]}
        />
        {posts.map(({ node }) => {
          return (
            <article className="post" key={node.fields.slug}>
              {node.frontmatter.img &&
                node.frontmatter.img.childImageSharp &&
                node.frontmatter.img.childImageSharp.fluid && (
                  <Link
                    to={node.fields.slug}
                    className="post-thumbnail"
                    style={{
                      backgroundImage: `url(${node.frontmatter.img.childImageSharp.fluid.src})`,
                    }}
                  />
                )}
              <div className="post-content">
                <div className="post-headeritem">
                  <span className="post-date">
                    {
                      new Intl.DateTimeFormat("es-ES", {
                        year: "numeric", month: "long", day: "2-digit"
                      }).format(new Date(node.frontmatter.date))
                    }&nbsp;&nbsp;—&nbsp;
                </span>
                  <span className="post-words">
                    {node.timeToRead} {node.timeToRead == 1 ? 'minuto' : 'minutos'}
                  </span>
                </div>
                <h2 className="post-title">
                  <Link to={node.fields.slug}>{node.frontmatter.title}</Link>
                </h2>
                <p>{node.frontmatter.description} {node.excerpt.toString().replace('Generic badge', '')}</p>
                <div className="post-tags">
                  {node.frontmatter.tags &&
                    node.frontmatter.tags.map(tag => (
                      <span key={tag}>
                        <Link className="tag" to={`/tags/${kebabCase(tag)}/`}>#{tag}</Link>
                      </span>
                    ))}
                </div>
              </div>
            </article>
          )
        })}
        <Pagination currentPage={currentPage} numPages={numPages} />
      </DefaultLayout>
    )
  }
}

export default BlogIndex

export const pageQuery = graphql`
  query blogPageQuery($skip: Int!, $limit: Int!) {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      limit: $limit
      skip: $skip
    ) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          timeToRead
          frontmatter {
            date(formatString: "YYYY, MMM DD")
            title
            description
            tags
            img {
              childImageSharp {
                fluid(maxWidth: 3720) {
                  aspectRatio
                  base64
                  sizes
                  src
                  srcSet
                }
              }
            }
          }
        }
      }
    }
  }
`
